.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser{
    height: 30px;
    background-color:rgb(243, 242, 242);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.p-name{
    margin-left: auto;
}

.p-desc{
    width: 30%;
    display: flex;
    justify-content: center;
    overflow: hidden;
}

.p-img{
    width: 100%;
    transition: all 6s ease;
}

.p:hover .p-img{
transform: translateY(-50%)
}

@media screen and (max-width: 480px) {
    .p {
      width: 200%;
      height: 35vh;
    }
    .p-browser{
        height: 45px;
    }
  }